nav {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 1rem ;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  background: rgba(47, 43, 43, 0.31);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.8px);
  -webkit-backdrop-filter: blur(5.8px);

}
nav div {
  font-size: 2rem;
  font-weight: 900;
}

.navItems {
  display: flex;
  list-style-type: none;
  align-items: center;
}
.navItems li {
  padding: 0 5px;
  font-weight: 700;
}
.navItems li:after {
  content: '';
  display: block;
  margin: auto;
  height: 3px;
  width: 0px;
  background: transparent;
  transition: width .5s ease, background-color .5s ease;
}

.navItems li:hover {
  cursor: pointer;
}
.navItems li:hover:after {
  width: 100%;
  background-color: #fff;  
}

@media (max-width:500px) {
  nav {
    display: flex;
    justify-content: center;
    padding: 1rem 2rem;
  }
  nav div {
    font-size: .8rem;
  }
  .nav-items {
    font-size: .8rem;
  }
}